<script>
import {helpers, required} from 'vuelidate/lib/validators'
import Group from './Group';

const alpha = helpers.regex('alpha', /^[a-zA-Zа-яёА-ЯЁ]*$/)
const phone = helpers.regex('phone', /\+7\d{10,}$/)


export default {
  name: 'Form',
  components: {
    Group
  },
  data() {
    return {
      form: {
        firstName: '',
        lastName: '',
        patronymicName: '',
        date: '',
        tel: '',
        sex: 'man',
        index: '',
        country: '',
        state: '',
        region: '',
        street: '',
        house: '',
        typeDocument: '',
        series: '',
        number: '',
        dateOfIssue: '',
        issuancePassport: '',
        sms: false,
        type: []
      }
    }
  },
  props: {
    title: String
  },
  methods: {
    submitForm() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        console.log('Не удалось отправить форму')
      } else {
        console.log('Форма отправлена', this.form)
        alert('Клиент успешно создан!')

      }
    },
    invalidClassname(validator) {
      return {'field__input--invalid': validator.$error}
    }
  },

  validations: {
    form: {
      firstName: {
        required,
        alpha
      },
      lastName: {
        required,
        alpha
      },
      date: {
        required
      },
      tel: {
        required,
        phone
      },
      state: {
        required,
        alpha
      },
      typeDocument: {
        required
      },
      dateOfIssue: {
        required
      }
    }

  },

}
</script>

